<template>
  <div id="update" class="scrollTouch">
    <div class="flex-row title">系统升级维护</div>
    <div class="time_update flex-row">{{commonState.updateData.maintenance_time}}</div>
    <div class="flex-row">
      <div class="iconSize system">
        <img src="../../assets/img/update/system.png" alt="系统维护图层">
      </div>
    </div>
    <div class="flex-row titleTip">掘金宝系统升级服务</div>
    <div class="updateBox">
      <div class="updateTitle posC">{{commonState.updateData.vsn}}升级内容</div>
      <div class="content" v-html="commonState.updateData.maintenance_info"></div>
    </div>
    <div class="flex-row logo">
      <div class="iconSize logoImage">
        <img src="../../assets/img/my_log.png" alt="掘金宝logo">
      </div>
      掘金宝
    </div>
  </div>
</template>
<script>
import {mapState, mapMutations} from 'vuex'
export default {
  data () {
    return {}
  },
  computed: mapState({
    commonState: state => state.common
  }),
  methods: {
    ...mapMutations(['common_set_updateData']),
    getUpdate () {
      this.$api.get('GET_UPDATE', {vsn: this.$config.version}, res => {
        this.$router.push('/')
      }, res => {
        if (res.data.code !== 707) {
          this.$router.push('/')
        } else {
          if (res.data.maintenance_time) {
            this.common_set_updateData(Object.assign({vsn: res.vsn}, res.data))
          }
        }
      })
    }
  },
  created () {
    this.getUpdate()
  }
}
</script>

<style lang="scss" scoped>
#update{
  box-sizing: border-box;
  overflow: auto;
  background: #f5f6f9;
  height: 100vh;
  box-sizing: border-box;
  color: #999;
  .title {
    padding: 64px 0 18px;
    line-height: 50px;
    font-size: 36px;
    color: #F37F32;
  }
  .time_update{
    font-size: 28px;
    line-height: 40px;
    color: #999;
  }
  .system{
    width: 342px;
    height: 402px;
    margin: 61px 0 115px;
  }
  .titleTip{
    font-size: 32px;
    color: #333;
  }
  .updateBox{
    width: 690px;
    min-height: 286px;
    max-height: 580px;
    position: relative;
    margin: 50px auto;
    border-radius: 15px;
    .updateTitle{
      transform: translate(-50%,-50%);
      top: 0;
      padding: 0 20px;
      max-width: 560px;
      overflow: hidden;
      text-align: center;
      color: #666666;
      font-size: 32px;
    }
    .content{
      font-size: 22px;
      padding: 40px 50px;
      font-size: 28px;
      line-height: 1.7;
      color: #999999;
    }
  }
  .logo{
    padding:  20px 0 30px;
    font-size: 30px;
    color: #FF7F18;
    .logoImage{
      width: 65px;
      height: 65px;
      margin-right: 10px;
    }
  }

}
</style>



